var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',[_vm._ssrNode("<main class=\"main\" data-v-3b218cc8><div class=\"detail\" data-v-3b218cc8><section class=\"ss-detail\" data-v-3b218cc8><div class=\"detail-main ss-pd\" data-v-3b218cc8><div class=\"container\" data-v-3b218cc8><div class=\"dsmall side\" data-v-3b218cc8><div class=\"dsmall-item side-nine\" data-v-3b218cc8><div class=\"detail-wrapper\" data-v-3b218cc8><div data-aos=\"fade-up-cus\" class=\"detail-block\" data-v-3b218cc8><div class=\"pro-wrap\" data-v-3b218cc8><div class=\"pro-content\" data-v-3b218cc8><h1 class=\"detail-pro-name pro-name\" data-v-3b218cc8>\n                          🎉🎉DANH SÁCH CÁC “CHIẾN BINH NHÍ” URI LỌT VÀO VÒNG CHUNG KẾT CUỘC GIA KÌ THI TOÁN HỌC QUỐC TẾ HKIMO\n                        </h1></div></div></div> <div data-aos=\"fade-up-cus\" class=\"detail-block mona-content post-content\" data-v-3b218cc8><p data-v-3b218cc8>🏆 𝐍𝐠𝐮𝐲𝐞̂̃𝐧 𝐇𝐮̛̃𝐮 𝐏𝐡𝐮́𝐜 𝐓𝐡𝐢̣𝐧𝐡, 𝐋𝐨̛́𝐩 𝟐 𝐓𝐇 𝐍𝐠𝐡𝐢 𝐏𝐡𝐮́ 𝟐 <br data-v-3b218cc8></p> <img src=\"https://scontent.fhan5-11.fna.fbcdn.net/v/t39.30808-6/335465072_207326931952449_1389999907602066251_n.jpg?_nc_cat=111&ccb=1-7&_nc_sid=730e14&_nc_ohc=5RfrGPxzE3QAX8ZbbXb&_nc_ht=scontent.fhan5-11.fna&oh=00_AfAvn-kL5vRcmkNuE1Li9BEXQEa4O8IW5iY4uJ3i4TLp1A&oe=64405462\" alt data-v-3b218cc8> <p data-v-3b218cc8>🏆 𝐍𝐠𝐮𝐲𝐞̂̃𝐧 𝐓𝐫𝐚̂𝐦 𝐀𝐧𝐡, 𝐋𝐨̛́𝐩 𝟐 𝐓𝐇 𝐋𝐞̂ 𝐋𝐨̛̣𝐢 <br data-v-3b218cc8></p> <img src=\"https://scontent.fhan5-11.fna.fbcdn.net/v/t39.30808-6/335721698_1310434166548832_6426534620213513743_n.jpg?_nc_cat=111&ccb=1-7&_nc_sid=730e14&_nc_ohc=dBWBSXdHQF0AX_XuqTj&_nc_ht=scontent.fhan5-11.fna&oh=00_AfB2PKcaUzsS-uxSZ9BW6U6CaRWxwd9R5c9v1pZ9WX5a2w&oe=643FC929\" alt data-v-3b218cc8> <p data-v-3b218cc8>🏆 𝐓𝐫𝐚̂̀𝐧 𝐍𝐠𝐮𝐲𝐞̂̃𝐧 𝐇𝐮̛𝐨̛𝐧𝐠 𝐓𝐫𝐚̀, 𝐋𝐨̛́𝐩 𝟐 𝐓𝐇 𝐍𝐠𝐮𝐲𝐞̂̃𝐧 𝐓𝐫𝐚̃𝐢<br data-v-3b218cc8></p> <img src=\"https://scontent.fhan5-2.fna.fbcdn.net/v/t39.30808-6/335649462_1378685862933656_4736633749211943738_n.jpg?_nc_cat=102&ccb=1-7&_nc_sid=730e14&_nc_ohc=uxzIdoxvK00AX-nNMPj&_nc_ht=scontent.fhan5-2.fna&oh=00_AfBwMzbu5HjnT14lblRNpDgBjlTFBKR2eIXtLuq7c_aLyg&oe=6440B58B\" alt data-v-3b218cc8> <p data-v-3b218cc8>🏆 𝐕𝐨̃ Đ𝐚̆𝐧𝐠 𝐊𝐡𝐚́𝐧𝐡, 𝐋𝐨̛́𝐩 𝟑 𝐏𝐓 𝐓𝐇𝐒𝐏 Đ𝐚̣𝐢 𝐡𝐨̣𝐜 𝐕𝐢𝐧𝐡 <br data-v-3b218cc8></p> <img src=\"https://scontent.fhan5-8.fna.fbcdn.net/v/t39.30808-6/336270114_599247655102574_6133951007844267580_n.jpg?_nc_cat=108&ccb=1-7&_nc_sid=730e14&_nc_ohc=y50ZemhAZwAAX8x48Bh&_nc_ht=scontent.fhan5-8.fna&oh=00_AfDmEgf3zyfQClgcAWWgviMWKiDPwq5wQHORCkNe7PueTQ&oe=644110A2\" alt data-v-3b218cc8> <p data-v-3b218cc8>🏆 𝐁𝐮̀𝐢 𝐏𝐡𝐮́ 𝐐𝐮𝐚𝐧𝐠, 𝐌𝐚̂̀𝐦 𝐧𝐨𝐧 𝐕𝐢𝐞𝐭𝐬𝐢𝐧𝐠 <br data-v-3b218cc8></p> <img src=\"https://scontent.fhan5-2.fna.fbcdn.net/v/t39.30808-6/335839765_607931707844711_8102091552658649319_n.jpg?_nc_cat=102&ccb=1-7&_nc_sid=730e14&_nc_ohc=j04Ka7U81x8AX8wrrne&_nc_ht=scontent.fhan5-2.fna&oh=00_AfA_tk7K5WHj6lKuL7ZaP0nJiFS6rMVyaG5CBN4KAQ6KHA&oe=644047C7\" alt data-v-3b218cc8> <p data-v-3b218cc8>\n                      💎 Kỳ thi Olympic Toán học quốc tế HKIMO (Hongkong International Mathematical Olympiad) là kỳ thi được tổ chức hàng năm nhằm mục đích tạo ra một sân chơi bổ ích dành cho tất cả học sinh yêu thích bộ môn Toán học. Bên cạnh việc kích thích và nuôi dưỡng niềm yêu thích Toán học, giúp tăng cường khả năng tư duy sáng tạo của học sinh, Kỳ thi HKIMO còn là cơ hội để các bạn trẻ thử thách chính bản thân mình và mở rộng mối quan hệ giao lưu văn hóa quốc tế.\n                    </p> <p data-v-3b218cc8>\n                      Đồng hành cùng các con là tinh thần đầy nhiệt huyết, tận tâm của các cô giáo URI, với mong muốn cuộc thi không chỉ mang lại\n                      cho các con những kiến thức quan trọng về Toán học mà còn giúp các con luôn tự tin và vững vàng khi đối mặt với những thử\n                      thách, luôn năng động, tư duy trong các hoạt động học tập.\n                    </p></div></div></div> <div class=\"dsmall-item side-three\" data-v-3b218cc8><div data-aos=\"fade-up-cus\" class=\"sidebar\" data-v-3b218cc8><div class=\"side-fixed\" data-v-3b218cc8><div class=\"sid